.overlay {
  position: fixed;
  z-index: var(--z-index-overlay);
  top: 0;
  left: 0;

  visibility: hidden;
  overflow: auto;

  width: 100%;
  height: 100vh;
  padding: var(--main-indent);

  // transition: display 0s 0.2s, opacity 0.2s ease;
  // transition: opacity 0.2s ease;

  opacity: 0;
  background: #fff;

  &.active {
    visibility: visible;

    opacity: 1;

    .overlay__body {
      transform: translateY(0);

      opacity: 1;
    }
  }

  &__close-button {
    position: fixed;
    z-index: 1;
    top: 0;
    right: 0;

    margin: var(--main-indent);
    padding: 15px;

    cursor: pointer;
    transition: border 0.2s linear;

    border: solid 1px transparent;
    background: transparent;

    @include for("650") {
      & {
        padding: 10px;
      }
    }

    @include for("400") {
      & {
        padding: 5px;
      }
    }

    &:hover,
    &:focus {
      border-color: var(--purple-light);
    }

    svg {
      width: 25px;
      height: 25px;
    }

    .close-x {
      stroke: var(--purple);
      fill: transparent;
      stroke-linecap: round;
      stroke-width: 5;
    }
  }

  .overlay__body {
    transition: opacity 0.3s 0.1s ease, transform 0.3s 0.15s ease;
    transform: translateY(5px);

    opacity: 0;

    display: flex;
    flex-flow: column nowrap;
    min-height: 100%;
    // justify-content: center;
  }
}
