.container {
  display: grid;
  grid-template-columns: 100%;

  @include for("900<") {
    &--33-33-33 {
      grid-template-columns: 30% 30% 30%;
    }

    &--50-50 {
      grid-template-columns: 50% 50%;
    }

    &--65-35 {
      grid-template-columns: minmax(0, 65%) minmax(350px, 35%);
    }

    &--70-30 {
      grid-template-columns: minmax(0, 70%) minmax(350px, 30%);
    }
  }

  &--column-gap {
    column-gap: var(--main-indent);
  }

  &--row {
    display: flex;
    flex-wrap: wrap;
    margin: calc(var(--negative-main-indent) / 2);

    .container__column {
      margin: calc(var(--main-indent) / 2);
    }
  }

  // &__column {
  //   border: dashed red 1px;
  // }

  &__aside {
    @include for("900") {
      display: none;
    }
  }

  &__wrapper {
    &--800 {
      max-width: 800px;
      margin-left: auto;
      margin-right: auto;
    }

    &--bg-blue-gradient {
      position: relative;

      &::after {
        position: absolute;
        z-index: -1;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        content: "";
        background: linear-gradient(
          180deg,
          rgba(0, 0, 0, 0) 0,
          rgba(0, 0, 0, 0) 44.999%,
          rgba(199, 212, 237, 0.6) 45%,
          rgba(244, 244, 244, 0) 100%
        );
        background-repeat: repeat-x;
        background-position: center;
        background-size: 10px 50%;
      }
    }
  }

  .--map-mode & {
    @include for("900") {
      grid-template-rows: auto 1fr;

      > .aside {
        display: block;
      }
    }
  }
}
