.book-form {
  margin: 20px 0;
  border: solid 1px var(--grey-light);

  &__header {
    padding: 20px 20px 0;
  }

  &__title {
    margin: 0;
    padding: 0;
    font-weight: bold;
  }

  &__body {
    display: flex;
    flex-wrap: wrap;
    padding: 10px;
  }

  &__input-group {
    flex-basis: 30%;
    flex-grow: 1;
    padding: 10px 10px;
    > .input {
      margin-top: 7px;
    }
    &--place {
      flex-basis: 100%;
      width: 100%;
    }
    &--datetime {
      @include for("650") {
        flex-basis: 100% ;
        width: 100%;
        > .input {
          width: 100%;
        }
      }
    }
    &--people {
      >input{
        width: 100%;
      }

      @include for("650") {
        flex-basis: 50%;
        width: 50%;
      }

@include for("400") {
        flex-basis: 100%;
        width: 100%;
      }
    }
  }

  &__footer {
    display: flex;
    align-items: center;
    flex-flow: row-reverse wrap;
    justify-content: flex-end;
    padding: 20px;
    background: var(--grey-lighter);
  }

  &__submit {
    margin-right: 20px;

    justify-self: flex-end;
  }

  &__footer-details {
    align-self: flex-start;
  }

  &__footer-detail {
    margin-right: 10px;
  }
}