:root {
  --tick-duration: 0ms;
  --tick-distance: 0;
}

@keyframes button-animation {
  0% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(-5px);
  }
  100% {
    transform: translateX(0);
  }
}

@keyframes ticker-animation {
  0% {
    overflow: visible;
    transform: translateX(0);
  }
  55% {
    overflow: visible;
    transform: translateX(var(--tick-distance));
  }
  60% {
    overflow: visible;
    transform: translateX(var(--tick-distance));
  }
  99% {
    overflow: visible;
    transform: translateX(0);
  }
  100% {
    overflow-x: auto;
    overflow-y: hidden;
    width: calc(100% + 30px);
    margin: 0 -15px;
    padding: 0 15px;
  }
}

//Header
.header {
  position: sticky;
  z-index: var(--z-index-header);
  top: -45px;
  margin-left: auto;
  margin-right: auto;
  max-width: var(--max-content-width);
  transition: transform 0.12s ease;
  transform: translateY(0);

  @include for("900") {
    top: 0;
  }

  .header-top {
    height: 45px;
    padding: 0 var(--main-indent);
    background: #fff;

    @include for("900") {
      height: 40px;
    }

    &__wrapper {
      display: flex;
      height: 100%;
      margin: 0 auto;

      &--desktop {
        @include for("900") {
          display: none;
        }
      }

      &--mobile {
        @include for("900<") {
          display: none;
        }
      }
    }

    //Desktop
    &__settings {
      display: flex;
      align-items: center;
      flex-grow: 1;

      & > :not(:last-child) {
        margin-right: 15px;
      }
    }
    &__navigation {
      display: flex;
      align-items: center;
      flex-grow: 1;
      justify-content: flex-end;

      &-list {
        display: flex;
        margin: 0;
        padding: 0;
        list-style: none;

        &-item {
          display: flex;
          align-items: center;
          margin: 0 15px;

          &:first-child {
            margin-left: 0;
          }

          &:last-child {
            margin-right: 0;
          }
        }
      }

      &-link {
        text-decoration: none;
        color: var(--text-dark);
        font-size: var(--fs-sm);
      }
    }

    //Mobile
    &__button-wrap {
      display: flex;
      align-items: center;
      height: 100%;
      &:first-child {
        margin-left: calc(0px - var(--main-indent));
      }
      &:last-child {
        margin-right: calc(0px - var(--main-indent));
      }
    }
    &__button {
      width: calc(40px + var(--main-indent));
      height: 39px;
      padding: 0;
      border: none;
      outline: none;
      background-color: #fff;
      background-repeat: no-repeat;
      background-position-y: center;
      background-size: 14px 14px;

      &--back {
        opacity: 0.15;
        background-image: $left_arrow;
        background-position-x: var(--main-indent);

        &.visible {
          opacity: 1;
        }
        &.active {
          animation: button-animation 0.25s ease forwards;
        }
      }

      &--share {
        background-image: $share_icon;
        background-position-x: calc(100% - var(--main-indent));
      }
    }
    &__window-title-wrapper {
      position: relative;
      display: flex;
      overflow: hidden;
      align-items: center;
      flex-grow: 1;
      height: 100%;
      margin: 0 -15px;
      padding: 0 15px;
      color: var(--grey-text);
      font-size: var(--fs-sm);

      &::before,
      &::after {
        position: absolute;
        z-index: 1;
        top: 0;
        left: 0;
        display: block;
        width: 15px;
        height: 90%;
        content: "";

        background-image: $title_gradient;
        background-repeat: repeat-y;
        background-position: center;
        background-size: cover;
      }

      &::after {
        right: 0;
        left: unset;
        transform: scale(-1, 1);
      }
    }
    &__window-title {
      display: inline-block;
      width: 100%;
      height: 100%;
      user-select: none;
      text-align: center;
      white-space: nowrap;
      line-height: 40px;

      &.ticker-animation {
        animation: ticker-animation var(--tick-duration) linear;
        animation-delay: 2s;

        animation-fill-mode: forwards;
      }
    }
  }

  .header-bottom {
    padding: 0 var(--main-indent);
    background: #381b4e;
    background: var(--purple-gradient);

    // Menu section
    &__menu {
      display: flex;
      align-items: center;
      min-height: 65px;
      margin: 0 auto;

      & > *:not(:last-child) {
        margin-right: 30px;

        @include for("1200") {
          margin-right: 15px;
        }
      }

      @include for("650") {
        min-height: 45px;
      }
    }

    // Logo
    &__logo-image {
      width: 73px;
      color: #fff;
      @include for("650") {
        width: 70px;
      }
    }

    // Navigation
    &__navigation {
      display: flex;
      align-items: center;
      flex-grow: 1;

      @include for("1200") {
        display: none;
      }

      &-list {
        display: flex;
        flex-wrap: wrap;
        margin: 0;
        list-style: none;

        &-item {
          display: flex;
          align-items: center;
          padding: 10px 15px;
        }
      }

      &-link {
        text-decoration: none;
        text-transform: uppercase;
        color: #fff;
        font-size: var(--fs-sm);
        font-weight: bold;
      }
    }

    // Actions
    &__actions {
      flex-grow: 1;
      justify-content: flex-end;
    }

    &__actions-list {
      display: flex;
      justify-content: flex-end;
      margin: 0;
      padding: 0;
      list-style: none;

      &-item:not(:first-child) {
        margin: 0 0 0 24px;
        @include for("650") {
          margin: 0 0 0 20px;
        }
      }

      &-item--menu-button {
        @include for("1200<") {
          display: none;
        }
      }
    }

    &__actions-link {
      display: flex;
      align-items: center;
      text-decoration: none;
      color: #fff;
      font-size: var(--fs-sm);

      &-title {
        margin-left: 10px;
        @include for("650") {
          display: none;
        }
      }
    }

    //
    // Icons
    //

    [class*="header-bottom__icon"] {
      position: relative;
      display: block;
      width: 30px;
      height: 30px;
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;

      &::after {
        position: absolute;
        bottom: 0;
        left: 50%;
        padding: 0 5px;
        content: attr(data-badge-text);
        transform: translate(-50%, 42%);
        white-space: nowrap;
        color: #000;
        border-radius: 100px;
        background: var(--gold);
        box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
        font-size: var(--fs-xs);
        font-weight: bold;
        line-height: 1.5;

        @include for("650") {
          transform: translate(-50%, 25%);
        }
      }
    }

    &__icon-bonus {
      background: $bonus_icon;
    }

    &__icon-profile {
      background: $profile_icon;
    }

    &__icon-cart {
      background: $cart_icon;
    }

    &__icon-menu {
      background: $menu_icon;
    }

    // &__icon-search {
    //   background: $search_icon;
    // }
  }

  //
  //Modification
  //

  &--center {
    .header-top__wrapper {
      max-width: 1300px;
    }

    .header-bottom__menu {
      max-width: 1300px;
    }
  }

  .--map-mode & {
    @include for("900") {
      display: none;
    }
  }
}
