.card {
	display: flex;
	flex-flow: column nowrap;
	width: 100%;
	min-width: 200px;
	max-width: 310px;

	@include for("400") {
		max-width: 280px;
	}

	//
	//
	// Image wrap
	//
	//

	&__image-wrap {
		position: relative;
		display: block;
		width: 100%;
		max-width: 310px;
		height: 100%;
		max-height: 200px;
		padding-bottom: calc(400 / 620 * 100%);
		background-color: var(--grey-light);

		//todo: заменить на svg по-хорошему бы
		// background-image: url("/img/common.blocks/card/placeholder.png");
		background-image: url(data:image/jpeg;base64,);
		background-repeat: no-repeat;
		background-position: center;
		background-size: 70%;
	}

	&__image-wrap:hover + &__info-wrap &__title {
		text-decoration: underline;
	}

	&__image {
		position: absolute;
		width: 100%;
		height: 100%;

		object-fit: cover;
	}

	//
	//
	// Info wrap
	//
	//

	&__info-wrap {
		display: flex;
		flex-wrap: wrap;
		padding-top: 10px;

		@include for("650") {
			padding-right: 10px;
		}

		& > * {
			margin: 0 0 10px;
		}
	}

	&__title {
		flex-basis: 100%;
		font-size: var(--fs-initial);
		font-weight: 500;

		&-link {
			text-decoration: none;
			color: inherit;
		}
	}

	&__price {
		flex-basis: 100%;
		// font-size: var(--fs-initial);
	}

	&__rating {
		display: flex;
		align-items: center;
	}

	&__badge-wrap {
		display: flex;
		flex-wrap: wrap;
		margin-right: 10px;
		> .badge {
			margin-right: 4px;
		}
	}

	&__custom-info {
		display: flex;
		align-items: center;
		color: var(--grey-text);
		font-size: var(--fs-sm);
	}

	&__button-wrap {
		flex-basis: 100%;

		> .button:not(:last-child) {
			margin-right: 5px;
		}
	}

	//
	//
	// MODIFICATIONS
	//
	//

	&--title-at-bottom {
		.card__title {
			order: 1;
			margin-bottom: 0;
			font-size: var(--fs-normal);
		}
	}

	&--scale-down-image {
		.card__image-wrap {
			border: solid 1px var(--grey-light);
			background: #fff;
		}

		.card__image {
			width: 100%;
			height: 100%;
			margin-bottom: 0;
			padding: 10px;

			object-fit: scale-down;
			object-position: center;
		}
	}

	&--squared-image {
		width: 145px;
		min-width: 145px;
		max-width: 145px;

		.card__image-wrap {
			width: 145px;
			height: 145px;
		}
	}

	&--show-only-image {
		.card__info-wrap {
			display: none;
		}
	}

	&--medium-size {
		width: 250px;
		min-width: 250px;
		max-width: 250px;

		.card__image-wrap {
			width: 250px;
			height: 160px;
		}
	}

	&--text-align-center {
		.card__info-wrap {
			justify-content: center;

			.card__button-wrap {
				text-align: center;
			}

			.card__title {
				flex-basis: auto;
				margin-right: 1em;
				font-size: var(--fs-normal);
			}

			.card__price {
				flex-basis: auto;
				font-size: var(--fs-normal);
			}
		}
	}

	&--shadow-at-bottom {
		.card__image-wrap {
			position: relative;
			border-color: var(--grey-lighter);
			border-radius: 10px;

			& > img {
				border-radius: 10px;
			}

			&::after {
				position: absolute;
				z-index: -1;
				bottom: -3px;
				left: -16px;
				display: block;
				width: calc(100% + 32px);
				height: 4px;
				content: "";
				border-radius: 50%;
				background: rgba(0, 0, 0, 0.25);

				filter: blur(3px);
			}

			&::before {
				position: absolute;
				z-index: -1;
				bottom: -0.5px;
				left: 1%;
				display: block;
				width: 98%;
				height: 2px;
				content: "";
				border-radius: 50%;
				background: rgba(0, 0, 0, 0.4);

				filter: blur(3px);
			}
		}
	}

	&--shadow {
		margin: 10px 0;
		padding: 10px;
		background: #fff;
		box-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
	}

	@mixin horizontal-view {
		overflow: hidden;
		align-items: center;
		flex-direction: row;
		width: 100%;
		min-width: unset;
		max-width: unset;

		.card__image-wrap {
			width: 150px;
			max-width: 150px;
			height: 100px;
			max-height: 100px;
			margin-right: 20px;
			padding-bottom: 0;
		}

		.card__info-wrap {
			width: calc(100% - 170px);
		}

		.card__title {
			padding-right: 5px;
		}

		.card__title,
		.card__price {
			overflow: hidden;
			white-space: nowrap;
			text-overflow: ellipsis;
			font-size: var(--fs-normal);
		}

		// @include for("400") {
		// 	min-width: 200px;
		// 	max-width: 250px;
		// }
	}

	&--horizontal-view-from-650 {
		@include for("650<") {
			@include horizontal-view;
		}
	}

	&--horizontal-view {
		@include horizontal-view;
	}

	&--map-layout {
		overflow: hidden;
		align-items: center;
		flex-direction: row;
		width: auto;
		min-width: 290px;
		max-width: calc(100vw - 30px);
		height: 120px;

		.card__image-wrap {
			max-width: 35%;
			height: 100px;
			margin-right: 10px;
			padding-bottom: 0;
		}

		.card__info-wrap {
			width: calc(65% - 10px);
			padding: 0;
		}

		.card__title {
			padding-right: 5px;
		}

		.card__badge-wrap {
			margin-bottom: 5px;
		}

		.card__badge-wrap,
		.card__title,
		.card__price {
			display: inline;
			overflow: hidden;
			flex-basis: 100%;
			white-space: nowrap;
			text-overflow: ellipsis;
			font-size: var(--fs-normal);
		}
	}
}