.fa-panel {
	bottom: var(--main-indent);
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
	align-items: flex-end;
	position: sticky;
	height: 0;
	z-index: 1;

	&__button {
		outline: none;
		background-color: #fff;
		border: 0;
		display: block;
		width: 56px;
		height: 56px;
		min-height: 56px;
		margin: 15px 0 0;
		transform: translateY(0);

		padding: 0;
		transition: opacity 0.3s ease, transform 0.3s ease, min-height 0.3s linear, margin 0.3s linear;
		opacity: 0.85;
		border-radius: 50%;
		box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.1), 0 3px 1px -2px rgba(0, 0, 0, 0.08), 0 1px 5px 0 rgba(0, 0, 0, 0.16);

		@include for("1200") {
			width: 48px;
			height: 48px;
			min-height: 48px;
		}

		&:hover:not(.fa-panel__button--hidden) {
			opacity: 1;
			cursor: pointer;
		}

		&--hidden {
			pointer-events: none;
			opacity: 0;
			transform: translateY(100%);
			min-height: 0px;
			margin: 0;
		}

		&--scroll-top {
			transform: rotate(90deg);
			background: #fff $left_arrow center no-repeat;
			background-size: 15px;
			box-shadow: 2px 0 2px 0 rgba(0, 0, 0, 0.1), 3px 0 1px -2px rgba(0, 0, 0, 0.08), 1px 0 5px 0 rgba(0, 0, 0, 0.16);

			@include for("900<") {
				background-size: 18px;
			}

			&.fa-panel__button--hidden {
				transform: rotate(90deg) translateX(50%);
			}
		}

		&--show-map {
			background: #fff $pointer_icon_purple center no-repeat;
			background-size: 18px;

			@include for("900<") {
				display: none;
			}
		}
	}

	.--map-mode & {
		@include for("900") {
			display: none;
		}
	}
}
