.content-block {
  .anchor {
    scroll-margin: calc(var(--header-height) + var(--main-indent));
  }

  &--center {
    margin: 0 auto;
  }

  > ul,
  > ol,
  > table {
    margin-block-end: 2em;
  }

  > ul {
    padding-inline-start: 2em;
    li {
      padding-left: 0.2em;
    }
    list-style-image: $bullet_point_gold;
  }
}
