.item-slider {
  width: 100%;

  .item-slider__title {
    // margin-bottom: .5em;
  }

  .swiper-container {
    order: 2;
    margin-bottom: 10px;
  }

  .item-slider__swiper-navigation {
    display: flex;
    align-items: center;

    .swiper-button {
      &--prev {
        margin-right: 10px;
      }
      &--next {
        margin-left: 10px;
      }
    }

    .swiper-pagination {
      position: relative;
    }
  }

  &--pagination-top {
    display: flex;
    flex-flow: wrap;
    justify-content: space-between;

    .item-slider__title {
      order: 0;
    }

    .item-slider__swiper-navigation {
      order: 1;

      @include for("650") {
        display: none;
      }
    }

    .swiper-container {
      order: 2;
    }
  }

  &--negative-main-indent-650 {
    @include for("650") {
      .swiper-container {
        margin-right: var(--negative-main-indent);
        margin-left: var(--negative-main-indent);
      }
    }
  }

  &--frameless {
    .swiper-container {
      padding-right: var(--main-indent);
      padding-left: var(--main-indent);
    }

    .item-slider__swiper-navigation,
    .item-slider__title {
      margin-right: var(--main-indent);
      margin-left: var(--main-indent);
    }
  }
}
