.mosaic-gallery {
  margin-left: 0;
  margin-right: 0;
  background: var(--grey-lighter);

  @include for("650") {
    display: flex;
    overflow-x: auto;

    scroll-snap-type: x proximity;

    &__tile {
      display: inline-block;
      width: 100%;
      max-width: 90vw;
      height: 30vh;
      max-height: 250px;
      margin-right: 5px;

      scroll-snap-align: start;

      & > img {
        width: auto;
        max-width: 90vw;
        height: 100%;

        object-fit: cover;
      }

      & > .video {
        min-width: 90vw;
      }
    }
  }

  @include for("650<") {
    position: relative;
    display: grid;

    grid-auto-flow: column;
    grid-template-columns: repeat(auto-fill, minmax(25%, 100%));
    grid-template-rows: repeat(2, minmax(120px, 9.2vw));

    &::after {
      position: absolute;
      top: 0;
      right: 0;
      display: block;
      padding: 10px 10px 10px 45px;
      content: attr(data-label);
      transition: all 0.4s ease;
      pointer-events: none;
      opacity: 0.75;
      color: #fff;
      border-radius: 0 0 0 5px;
      background-color: rgba(0, 0, 0, 0.5);
      background-image: $camera_icon;
      background-repeat: no-repeat;
      background-position: 10px center;
      background-size: 23px 23px;
      text-shadow: 0 0 3px black;
      font-size: var(--fs-normal);
    }

    &:hover::after {
      opacity: 1;
      background-color: rgba(0, 0, 0, 0.6);
    }

    &__tile {
      overflow: hidden;

      &:nth-child(5) ~ & {
        display: none;
      }

      &:first-child {
        grid-row-end: span 2;
      }

      &:hover {
        > img {
          cursor: pointer;
          transition: transform 1s ease;
          transform: scale(1.04);
        }
      }
      img {
        width: 100%;
        height: 100%;
        transition: transform 2.5s linear;

        object-fit: cover;
      }
    }
  }
}
