.main-menu {
  display: flex;
  align-self: center;
  flex-flow: column;
  flex-grow: 1;
  justify-content: center;

  max-width: 300px;

  &__list {
    margin: 5px 0;
    padding: 0;

    list-style: none;
  }

  &__list-item {
    margin: 15px 0;
  }

  &__link {
    text-decoration: none;

    color: var(--dark-text);

    &--uppercase {
      text-transform: uppercase;

      color: var(--purple-dark-text);

      font-weight: 500;
    }
  }
}
