.search {
	max-width: var(--max-content-width);
	margin-right: auto;
	margin-left: auto;

	&__form {
		display: flex;
		flex-flow: row nowrap;
		border-radius: 3px;
		background: #fff;

		@include for("650") {
			& {
				flex-flow: row wrap;
				background: none;
			}
		}
	}

	&__box {
		height: 40px;
		border-right: #d2daea solid 1px;

		@include for("650") {
			& {
				margin-bottom: 15px;
				border-right: none;
			}
		}

		&--region {
			flex-grow: 1;

			@include for("650") {
				& {
					width: 100%;
					border-radius: 3px;
				}
			}

			.search__input {
				padding-left: 35px;
				background: #fff $pointer_icon no-repeat 15px center;
				background-size: 12px 16px;
			}
		}

		&--category {
			flex-grow: 1;

			@include for("650") {
				& {
					width: 100%;
				}
			}
		}

		&--query {
			flex-grow: 2;

			@include for("650") {
				.search__input {
					border-radius: 3px 0 0 3px;
				}
			}
		}

		&--button {
			width: 40px;
			border-right: none;
			.search__input {
				border-radius: 0 3px 3px 0;
			}
		}
	}

	&__input {
		width: 100%;
		height: 100%;
		padding: 0 12px;
		border: none;
		border-radius: 3px;
		background: #fff;
		font-size: var(--fs-sm);
	}

	&__submit-button {
		cursor: pointer;
		background: #fff $lens_icon no-repeat center;
		background-size: 21px;

		// &-text {
		//   display: none;
		// }
	}

	&--center > * {
		max-width: 1300px;
		margin-right: auto;
		margin-left: auto;
	}

	&--bg-purple-gradient {
		padding: 0 var(--main-indent);
		background: var(--purple-gradient);

		@include for("1200") {
			margin: 0;
		}

		.common-queries__label,
		.common-queries__link {
			color: #fff;
		}
		.common-queries__item {
			border-right: 1px solid hsla(0, 0%, 100%, 0.15);
		}
	}

	&--shadow {
		filter: drop-shadow(0px 0px 10px rgba(0, 0, 0, 0.1));
	}

	.--map-mode & {
		&__box--region {
			@include for("900") {
				display: none;
			}
		}
	}
}

@import "common-queries/common-queries";
