.swiper {
  position: relative;
}

.swiper-slide {
  width: auto;
}

.swiper-button {
  width: 35px;
  height: 35px;
  cursor: pointer;
  border: solid 1px var(--purple-light);
  border-radius: 50%;
  background: #fff $left_arrow no-repeat center;

  &--prev {
    // <-
  }

  &--next {
    // ->
    transform: scale(-1, 1);
    background-position: calc(50% - 1px);
  }
}

.swiper-pagination {
  position: relative;
  line-height: 6px;
}

.swiper-bullet {
  display: inline-block;
  width: 6px;
  height: 6px;
  margin: 0 2.5px;
  transition: 0.3s ease background;
  vertical-align: middle;
  border-radius: 50%;
  background: var(--purple);

  &--active {
    background: var(--grey);
  }
}
