.d-none {
  display: none !important;
}

.d-none-to-900 {
  @include for("900") {
    display: none !important;
  }
}

.d-none-from-900 {
  @include for("900<") {
    display: none !important;
  }
}

@for $i from 0 to 4 {
  //MARGIN
  .m#{$i} {
    margin: calc(var(--main-indent) * #{$i});
  }

  .x-m#{$i} {
    margin-left: calc(var(--main-indent) * #{$i});
    margin-right: calc(var(--main-indent) * #{$i});
  }

  .y-m#{$i} {
    margin-top: calc(var(--main-indent) * #{$i});
    margin-bottom: calc(var(--main-indent) * #{$i});
  }

  .l-m#{$i} {
    margin-left: calc(var(--main-indent) * #{$i});
  }

  .r-m#{$i} {
    margin-right: calc(var(--main-indent) * #{$i});
  }

  .t-m#{$i} {
    margin-top: calc(var(--main-indent) * #{$i});
  }

  .b-m#{$i} {
    margin-bottom: calc(var(--main-indent) * #{$i});
  }

  // PADDING
  .p#{$i} {
    padding: calc(var(--main-indent) * #{$i});
  }

  .x-p#{$i} {
    padding-left: calc(var(--main-indent) * #{$i});
    padding-right: calc(var(--main-indent) * #{$i});
  }

  .y-p#{$i} {
    padding-top: calc(var(--main-indent) * #{$i});
    padding-bottom: calc(var(--main-indent) * #{$i});
  }

  .l-p#{$i} {
    padding-left: calc(var(--main-indent) * #{$i});
  }

  .r-p#{$i} {
    padding-right: calc(var(--main-indent) * #{$i});
  }

  .t-p#{$i} {
    padding-top: calc(var(--main-indent) * #{$i});
  }

  .b-p#{$i} {
    padding-bottom: calc(var(--main-indent) * #{$i});
  }
}
