.footer {
  font-size: var(--fs-sm);
  padding: 40px var(--main-indent);
  color: #fff;
  background: var(--purple-gradient);
  margin-left: auto;
  margin-right: auto;
  max-width: var(--max-content-width);

  &__content-wrap {
    display: flex;
    flex-wrap: wrap;
    max-width: 1300px;
  }

  &__navigation {
    flex-basis: 600px;
    margin: 0 30px 15px 0;

    &-list {
      margin: 0;
      column-count: 3;
      column-gap: 30px;

      @include for("650") {
        column-count: 2;
      }

      @include for("400") {
        column-count: 1;
      }
    }

    &-item {
      display: inline-block;
      width: 100%;
      margin-bottom: 15px;
      line-height: 1.2em;
    }

    &-link {
      text-decoration: none;
      color: #fff;
    }
  }

  &__infoblock {
    margin: 0 30px 15px 0;
    &-title {
      display: inline-block;
      margin-bottom: 15px;
    }
  }

  &__payment-icon {
    display: inline-block;
    margin: 0 6px 10px 0;
    max-width: 50px;
    opacity: 0.65;
    &:hover {
      opacity: 1;
    }
  }

  &__copy {
    flex-basis: 100%;
  }

  //MODS

  &--center &__content-wrap {
    margin: 0 auto;
  }

  .--map-mode & {
    @include for("900") {
      display: none;
    }
  }
}
