table {
  overflow-x: auto;
  display: block;
  border: solid 1px transparent;
  border-collapse: collapse;

  tr {
    border-bottom: solid 1px var(--grey-light);
  }

  table > tr:last-child,
  tbody > tr:last-child {
    border-bottom: solid 1px transparent;
  }

  th,
  td {
    min-width: 150px;
    max-width: 50vw;
    text-align: left;

    @include for("650") {
      min-width: 100px;
    }
  }

  th,
  td {
    padding: 1em 2.5em 1em 0;

    &:last-child {
      padding-right: 0;
    }
  }
}
