.offer-carousel {
  .swiper-wrapper {
    align-items: center;
  }

  .swiper-pagination {
    position: absolute;
    right: 15px;
    bottom: 15px;
    display: flex;
    justify-content: flex-end;

    @include for("650") {
      position: relative;
      right: 0;
      justify-content: center;
      width: 100%;
    }
  }
}
