.aside {
  position: relative;
  // Необходимо для тени и для абсолютного
  // позиционирования объектов внутри aside

  &--bg-gray {
    background: var(--grey-lighter);
    min-height: 100vh;
  }

  &--inset-shadow {
    &:after {
      position: absolute;
      z-index: 400;
      top: 0;
      left: 0;
      width: 100%;
      height: 20px;
      content: "";
      pointer-events: none;
      background: linear-gradient(rgba(0, 0, 0, 0.1) 0%, rgba(255, 255, 255, 0) 100%);

      @include for("900<") {
        width: 20px;
        height: 100%;
        background: linear-gradient(90deg, rgba(0, 0, 0, 0.1) 0%, rgba(255, 255, 255, 0) 100%);
      }
    }
  }

  &--stick-after-scroll {
    @include for("900<") {
      height: max-content;
      position: sticky;
      top: calc(100vh - var(--aside-height));
    }
  }

  &__container {
    margin-bottom: 10px;
    padding: 10px 30px;
    border: solid 1px var(--grey-light);

    @include for("650") {
      margin: 0 var(--negative-main-indent);
      padding: 10px var(--main-indent);
      border: none;
      border-top: solid 1px var(--grey-light);
    }
  }

  &__adress-map{
    padding: 0 var(--main-indent);

  }
}
