.gallery {
  .swiper-container {
    border: solid 1px var(--grey-light);
  }

  .swiper-slide {
    overflow: hidden;
    height: auto;
    align-self: stretch;

    img {
      max-height: 60vh;
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &__swiper-navigation {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 15px 0;

    .swiper-button, .swiper-pagination {
      position: relative;
      margin: 0 10px;
    }
  }

  &--bg-blur {
    .swiper-container {
      border: none;
    }

    .swiper-slide {
      position: relative;
      --bg-image: #fff;

      &::after {
        content: "";
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        transform: scale(1.1);
        filter: blur(7px);
        height: 100%;
        width: 100%;
        background: var(--bg-image);
        background-position: center;
        background-size: cover;
        z-index: -1;
      }
    }
  }
}
