.item-banner {
  display: flex;
  justify-content: center;
  height: 360px;

  background-color: var(--grey-lighter);
  background-image: url("../img/common.blocks/_item.blocks/item-banner/background.jpg");
  background-position: bottom center;
  background-size: cover;

  @include for("400") {
    height: 300px;
  }
  &__card-logo {
    position: relative;
    z-index: 0;
    // overflow: hidden;
    align-self: center;
    width: 310px;
    height: 200px;

    @include for("400") {
      width: 260px;
      height: 168px;
    }

    &-image {
      z-index: 1;
      display: flex;
      overflow: hidden;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: 100%;
      padding: 30px 40px;
      border-radius: 15px;
      background-color: #fff;

      img {
        width: 100%;
        height: 100%;

        object-fit: scale-down;
      }
    }

    &-shadow {
      z-index: -1;

      &::before {
        position: absolute;
        z-index: -1;
        bottom: -1px;
        left: 1%;
        display: block;
        width: 98%;
        height: 2px;
        content: "";
        border-radius: 50%;
        background: rgba(0, 0, 0, 0.5);

        filter: blur(3px);
      }
      &::after {
        position: absolute;
        z-index: -1;
        bottom: -3px;
        left: -25px;
        display: block;
        width: calc(100% + 50px);
        height: 7px;
        content: "";
        border-radius: 50%;
        background: rgba(0, 0, 0, 0.8);

        filter: blur(3px);
      }
    }
  }
}
