.catalog-view-switcher {
  display: inline-block;
  height: 30px;
  white-space: nowrap;
  font-size: 0;
  label {
    position: relative;
    display: inline-block;
    line-height: 0;
  }

  input {
    position: absolute;
    z-index: -1;
    width: 100%;
    height: 100%;
    pointer-events: none;
    opacity: 0;
  }

  input + span {
    display: block;
    cursor: pointer;
  }

  input:checked {
    opacity: 1;
  }

  input:checked + span {
    position: relative;
    cursor: auto;
    pointer-events: none;
  }

  input[value="list"]:checked + span,
  input[value="list"] + span {
    &::after {
      position: absolute;
      z-index: 1;
      display: block;
      width: 30px;
      height: 30px;
      content: "";
      transition: transform 0.15s ease;
      pointer-events: none;
      border: solid 1px var(--purple-light);
      border-radius: 3px;
    }
  }

  input[value="list"]:checked + span {
    &::after {
      transform: translateX(0);
    }
  }

  input[value="list"] + span {
    &:after {
      transform: translateX(100%);
    }
  }

  .catalog-view-switcher-icon {
    display: inline-block;
    width: 30px;
    height: 30px;
    background-color: #fff;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 14px 14px;
    &--list-icon {
      background-image: $grid-switcher-list-icon;
    }
    &--grid-icon {
      background-image: $grid-switcher-grid-icon;
    }
  }

  @include for("650") {
    & {
      display: none;
    }
  }
}
