.content-tabs {
  &__bar {
    display: flex;
    border-bottom: solid 1px var(--grey-lighter);

    &--sticky {
      background: #fff;
      position: sticky;
      top: var(--header-height);
      z-index: 1;
    }
  }

  &__bar-item {
    &:not(:last-child) {
      margin-right: 30px;
    }
  }

  &__trigger {
    display: block;
    padding: 15px 0;
    transition: all 0.25s ease;
    text-decoration: none;
    color: var(--grey-text);
    border-bottom: solid 4px transparent;
    font-weight: bold;
    position: relative;

    &::after {
      pointer-events: none;
      font-size: var(--fs-xs);
      font-weight: 300;
      content: attr(data-text);
      position: absolute;
      white-space: nowrap;
      color: var(--grey);
      padding-left: 10px;
      left: 100%;
      top: 51%;
      transform: translateY(-50%);
      opacity: 1;
      transition: opacity 0.2s ease;
    }

    &:hover {
      text-decoration: none;
      border-bottom: solid 4px var(--grey-light);
    }

    &.active {
      color: var(--dark-text);
      border-bottom: solid 4px var(--gold);
      &::after {
        opacity: 0;
      }
    }
  }

  &__container {
    scroll-margin: calc(var(--header-height) + 100px);
    display: none;
    &.active {
      display: block;
    }
  }
}
