@mixin grid_view {
	margin-right: -10px;
	margin-left: -10px;
	justify-content: space-evenly;

	@include for("1200") {
		justify-content: center;
	}

	//WRAPPER
	.catalog-card-wrapper {
		padding: 0 10px;
	}

	//CARD
	.card .card__info-wrap {
		visibility: visible;
		height: auto;
		// transition: visibility 0s linear, opacity 0.15s ease, transform 0.15s ease;
		// transform: translateY(0);
		opacity: 1;
	}

	//ASIDE
	.catalog-card-aside {
		visibility: hidden;
		overflow: hidden;
		width: 0;
		height: 0;
		// margin-left: 0;
		// transition: overflow 0s linear, margin-left 0s 0.15s linear, width 0s 0.15s linear, height 0s 0.15s linear,
		// 	visibility 0s 0.15s linear, opacity 0.15s ease, transform 0.15s ease;
		// transform: translateX(-5px);
		opacity: 0;
	}
}

@mixin list_view {
	//WRAPPER
	.catalog-card-wrapper {
		width: 100%;
	}

	//CARD
	.card .card__info-wrap {
		visibility: hidden;
		// height: 0;
		// transition: visibility 0s 0.15s linear, height 0s 0.15s linear, opacity 0.15s ease, transform 0.15s ease;
		// transform: translateY(-10px);
		opacity: 0;
	}

	//ASIDE
	.catalog-card-aside {
		visibility: visible;
		overflow: visible;
		width: 100%;
		height: 100%;
		margin-left: 20px;
		// transition: overflow 0s linear, margin-left 0s linear, width 0s linear, height 0s linear, visibility 0s linear,
		// 	opacity 0.15s ease, transform 0.15s ease;
		// transform: translateX(0);
		opacity: 1;
	}
}

.catalog__grid {
	display: flex;
	flex-flow: row wrap;
	margin: 20px 0;

	&--grid-view {
		@include grid_view;
	}

	&--list-view {
		//AT 650
		//SHOW GRID VIEW
		@include for("650") {
			@include grid_view;
		}

		//AT 650<
		//SHOW GRID VIEW
		@include for("650<") {
			@include list_view;
		}
	}

	.--map-mode & {
		@include for("900") {
			display: none;
		}
	}
}
