.catalog-map {
	z-index: 0; // for aside shadow
	height: calc(100vh - 65px);

	@include for("900") {
		height: 100%;
	}

	&__close-button {
		@include for("900<") {
			display: none;
		}

		position: absolute;
		z-index: 500; // leaflet map z-index
		top: 10px;
		right: 10px;
		display: flex;
		align-items: center;
		color: var(--purple);
		background: white;
		box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
		font-size: var(--fs-normal);

		img {
			margin-right: 9px;
			margin-left: 0;
			padding-bottom: 2px;
		}
	}
}
