.spoiler {
  ::-webkit-details-marker {
    display: none;
  }
  ::marker {
    display: none;
  }

  &__button::before {
    display: inline-block;
    line-height: 100%;
    margin-right: 1em;
    content: $left_arrow;
    transition: transform 0.2s ease;
    transform: rotate(180deg);
  }

  &[open] {
    .spoiler__button::before {
      transform: rotate(270deg);
    }
  }
}


