.offers-grid {
  display: grid;
  max-width: 1300px;
  margin: 0 auto;
  width: calc(100% - var(--main-indent) * 2);

  grid-gap: 20px;
  grid-template-columns: 640px auto;
  grid-template-areas:
    "carousel infoblock"
    "items infoblock";

  @include for("1200") {
    justify-content: center;

    grid-template-columns: minmax(calc(100% - 100px), 800px);
    grid-template-areas:
      "carousel"
      "items"
      "infoblock";
  }

  //CAROUSEL
  &__carousel {
    border: 1px solid var(--grey-light);

    grid-area: carousel;
  }

  //INFOBLOCK
  &__infoblock {
    display: flex;
    align-items: center;
    padding: 50px;
    border: 1px solid var(--grey-light);

    grid-area: infoblock;

    @include for("650") {
      padding: 20px;
    }

    &-article-title {
      margin-top: 0;
      font-size: var(--fs-h2);
      font-weight: bold;
      line-height: 37px;
    }

    &-article-paragraph {
      @include for("1200") {
        & {
          max-width: 700px;
        }
      }
    }

    &-article-list {
      max-width: 530px;
      padding: 0;

      column-count: 2;
      column-gap: 40px;

      @include for("400") {
        & {
          column-count: 1;
        }
      }
    }

    &-article-list-item {
      margin: 0 0 16px 0;
      list-style: none;

      & a {
        color: var(--purple);
        font-size: var(--fs-normal);
      }
    }
  }

  //ITEMS
  .offers-grid__items {
    display: flex;
    justify-content: space-between;
    grid-area: items;

    @include for("1200") {
      & {
        justify-content: space-evenly;
        margin: 0 -10px;
        > * {
          margin: 0 10px;
        }
      }
    }

    @include for("650") {
      & {
        display: none;
      }
    }
  }
}
