.button {
  display: inline-block;
  padding: 5.5px 17.5px;
  cursor: pointer;
  user-select: none;
  vertical-align: middle;
  text-decoration: none;
  color: var(--purple);
  border: 1px solid var(--purple-light);
  border-radius: 3px;
  background: transparent;
  font-size: var(--fs-normal);
  line-height: 23px;

  &[disabled],
  &:disabled {
    pointer-events: none;
    opacity: 0.6;
  }

  &--small {
    padding: 3.5px 15px;
    font-size: var(--fs-sm);
    line-height: 23px;
  }

  &--big {
    padding: 7.5px 20px;
    font-size: var(--fs-initial);
    line-height: 23px;
  }

  &--primary {
    color: #fff;
    border-color: transparent;
    background: var(--purple);
  }

  &--narrow,
  &--narrow-fill {
    padding: 4.5px 20px;
    transition: background-color 0.2s linear, box-shadow 0.2s ease;
    color: #000;
    border: none;
    box-shadow: inset 0 0 0 1px var(--purple-light);
    font-size: var(--fs-sm);

    &.active {
      background-color: rgba(0, 0, 0, 0.05);
      box-shadow: inset 0 1px 3px 1px rgba(0, 0, 0, 0.2);
    }
  }

  &--narrow-fill {
    background-color: var(--purple-light);
    &.active {
      background-color: var(--purple-light);
    }
  }

  &--inverse {
    color: #fff;
    border: 1px solid rgba(255, 255, 255, 0.5);

    &.button--primary {
      color: var(--purple);
      background: #fff;
      border-color: #fff;
    }

    &.button--narrow,
    &.button--narrow-fill {
      border: none;
      box-shadow: inset 0 0 0 1px rgba(255, 255, 255, 0.3);
      font-size: var(--fs-sm);

      &.active {
        background-color: rgba(0, 0, 0, 0.1);
        box-shadow: inset 0 1px 3px 1px rgba(0, 0, 0, 0.3);
      }
    }

    &.button--narrow-fill {
      background-color: rgba(255, 255, 255, 0.17);
      box-shadow: inset 0 0 0 1px transparent;

      &.active {
        background-color: rgba(255, 255, 255, 0.12);
        box-shadow: inset 0 1px 3px 1px rgba(0, 0, 0, 0.5);
      }
    }
  }
}

