.catalog {
  display: flex;
  flex-flow: column nowrap;
  padding: 0 var(--main-indent);

  //Общие элементы

  &__header {
    margin: 0 calc(0px - var(--main-indent));
    padding: 15px var(--main-indent) 0;

    background: var(--grey-lighter);
  }

  &__grid {
    flex-grow: 1;

    &--content-height {
      flex-grow: initial;
    }
  }

  &__footer {
    padding: 20px 0;

    @include for("1200") {
      & {
        padding: var(--main-indent) 0 60px;
      }
    }
  }
  .--map-mode & {
    @include for("900") {
      padding-bottom: 0;

      &__footer {
        display: none;
      }
    }
  }
}

@import "catalog__grid";
