.tabs {
  display: grid;
  justify-content: center;
  max-width: var(--max-content-width);
  margin-right: auto;
  margin-left: auto;
  padding: 40px var(--main-indent) 0;

  grid-template-columns: minmax(auto, 600px) 640px;
  grid-template-rows: auto auto 1fr;
  grid-gap: 20px;
  grid-column-gap: 60px;
  grid-template-areas: "title cards" "buttons cards" "text cards";

  @include for("1200") {
    grid-template-areas: "title" "buttons" "cards" "text";
    grid-template-columns: minmax(calc(100% - 100px), 800px);
  }

  &__title {
    margin: 0;

    grid-area: title;
  }

  &__bar {
    display: flex;
    flex-flow: row wrap;
    margin: 0;
    padding: 0;
    list-style: none;

    grid-area: buttons;

    &-item {
      margin: 0 5px 5px 0;
      &:last-child {
        margin-right: 0;
      }
    }
  }

  &__text-cont {
    grid-area: text;
  }

  &__cards-cont {
    display: grid;

    grid-area: cards;
    grid-gap: 20px;
    grid-template-columns: 1fr 1fr;

    @include for("1200") {
      justify-content: start;

      grid-template-columns: auto auto;
    }

    @include for("650") {
      display: inline-block;
      overflow-x: scroll;
      margin: 0 var(--negative-main-indent);
      padding: 0 var(--main-indent) 20px;
      white-space: nowrap;

      scroll-padding-left: var(--main-indent);
      scroll-snap-type: x proximity; //так адекватнее сопротивление на свайп (чем mandatory)

      & > .card {
        display: inline-flex;
        margin-right: 1em;
        white-space: normal;

        scroll-snap-align: start;
      }
    }
  }

  //MODS

  &--cards-in-row {
    grid-template-areas: "title" "buttons" "cards";
    grid-template-columns: minmax(auto, 1300px);

    //Mixed ELEMENTS
    .tabs__cards-cont {
      grid-template-columns: 1fr 1fr 1fr 1fr;

      @include for("1300") {
        justify-content: left;

        grid-template-columns: auto auto;
        justify-items: start;
      }
    }
  }

  &--bg-purple-gradient {
    margin-top: 40px;
    padding-bottom: 40px;
    color: #fff;
    background: var(--purple-gradient);

    @include for("1200") {
      grid-template-columns: minmax(calc(100% - 100px), 800px);
    }
  }

  &--cards-in-slider {
    position: relative;
    overflow: hidden;
    // width: 100%;
    // max-width: calc(100% + var(--main-indent) * 2);

    grid-template-areas: "title" "buttons" "cards";
    grid-template-columns: minmax(0, 1300px);

    @include for("1200") {
      grid-template-columns: minmax(calc(100% - 100px), 800px);
    }

    @include for("650") {
      grid-template-columns: 100%;
    }

    //Transitions for MODs
    //

    // .tabs__cards-cont:not(.active) {
    // .card img {
    // opacity: 0;
    // }
    // }

    .swiper {
      display: block;
      overflow: visible;

      .swiper-slide {
        width: auto;
      }

      .swiper-container {
        overflow: visible;
        width: 100%;
      }

      .swiper-pagination {
        position: absolute;
        bottom: -30px;
        left: 50%;
        display: none;
        transform: translateX(-50%);

        @include for("650") {
          display: flex;
        }
      }
    }

    //MODIFICATION for .tabs--cards-in-slider

    &.tabs--bg-blue-gradient::after {
      position: absolute;
      z-index: -1;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      content: "";
      background: linear-gradient(
        180deg,
        rgba(0, 0, 0, 0) 0,
        rgba(0, 0, 0, 0) 64.999%,
        rgba(199, 212, 237, 0.6) 65%,
        rgba(244, 244, 244, 0) 100%
      );
      background-repeat: repeat-x;
      background-position: center;
      background-size: 10px 50%;
    }
  }

  //Transitions for content

  &__cards-cont {
    // z-index: 0;
    visibility: hidden;
    transition: visibility 0s 0.3s;

    .card {
      transition: opacity 0.2s 0.1s linear;
      opacity: 0;
    }
  }

  &__cards-cont.active {
    z-index: 1;
    visibility: visible;
    transition: visibility 0s;

    .card {
      transition: opacity 0.2s linear;
      opacity: 1;
    }
  }
}
