.offer-banner {
  display: flex;
  align-items: center;

  padding: 50px;

  @include for("650") {
    & {
      flex-direction: column;

      padding: 30px 20px 36px;
    }
  }

  &__image-wrap {
    width: 240px;
    min-width: 240px;

    @include for("650") {
      & {
        height: 180px;
      }
    }
  }

  &__image-wrap {
    display: block;

    width: 50%;
    height: 100%;
    object-position: center;
    @include for("650") {
      width: 100%;
    }
  }

  &__image {
    width: 100%;
    height: 100%;
    max-height: 180px;

    object-fit: scale-down;
  }

  &__text-wrap {
    padding: 0 20px;

    @include for("650") {
      & {
        text-align: center;
      }
    }
  }

  &__text {
    padding-bottom: 15px;

    font-size: var(--fs-h3);
    font-weight: 500;
    line-height: 27px;
  }
}
