.main {
  max-width: 1360px;
  margin: 0 auto;
  padding-left: var(--main-indent);
  padding-right: var(--main-indent);

  &--fluid {
    width: 100%;
    max-width: var(--max-content-width); //4k max!
    padding-left: 0;
    padding-right: 0;
  }

  .--map-mode & {
    @include for("900") {
      height: 100vh;
    }
  }
}
