.review {
  display: grid;
  grid-template-columns: 60px 1fr;
  grid-template-areas:
    "userpic author"
    "userpic details"
    "text text"
    "gallery gallery";
    column-gap: 20px;
    align-items: center;
    

  margin-bottom: 40px;

  &__userpic {
    grid-area: userpic;
    height: 60px;
    width: 60px;
    border-radius: 50%;
    background-image: $userpic_icon;
    background-size: contain;
    display: flex;
    justify-content: center;

    img {
      height: 60px;
      width: 60px;
      object-fit: cover;
      border-radius: 50%;
    }
  }

  &__user {
    height: 30px;
    grid-area: author;
    display: flex;
    align-items: center;
    &name {
      font-size: var(--fs-normal);
      font-weight: bold;
    }
  }

  &__details {
    min-height: 30px;
    grid-area: details;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    font-size: var(--fs-normal);
  }

  &__details-time {
    margin-right: 15px;
  }

  &__details-rating-label {
    margin-right: 5px;
    color: var(--grey-text);
  }

  &__text {
    grid-area: text;
    margin: 5px 0;
  }

  &__gallery {
    grid-area: gallery;
    overflow-x: auto;
    white-space: nowrap;
    padding-bottom: 5px;

    scroll-snap-type: x proximity;
    > img {
      display: inline-block;
      height: 50px;
      cursor: pointer;

      scroll-snap-align: start;
    }
  }
}
