.tags-panel {
  display: inline-block;
  margin: 0;
}

.tag {
  display: inline-flex;
  align-items: center;
  position: relative;

  height: 30px;
  padding: 0 0 0 10px;

  border-radius: 3px;
  background-color: var(--blue);

  margin-bottom: 5px;
  margin-right: 5px;

  transform: scale(1);
  opacity: 1;
  transition: opacity 0.2s ease, transform 0.2s ease, margin-right 0.2s 0.05s ease;

  &--fade-out {
    transform: scale(0.85);
    opacity: 0;
  }

  &__text {
    white-space: nowrap;

    color: #fff;

    font-size: var(--fs-sm);
  }

  &__cross {
    height: 30px;
    width: 30px;
    background-image: $white_cross;
    background-size: 10px;
    background-position: center;
    background-repeat: no-repeat;
    background-color: transparent;
    border: none;
    cursor: pointer;
  }
}
