.video {
  position: relative;
  width: 100%;
  height: 100%;
  // padding-bottom: calc(1080 / 1920 * 100%);
  background-color: #000;
}

.video__link {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  outline-width: medium;
}

.video__media {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
  object-fit: cover;
}

.video__button {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 1;
  display: none;
  padding: 0;
  width: 68px;
  height: 48px;
  border: none;
  background-color: transparent;
  transform: translate(-50%, -50%);
  cursor: pointer;
}

.video__button-shape {
  fill: #212121;
  fill-opacity: 0.8;
  transition: fill 0.2s ease, fill-opacity 0.2s ease;
}

.video__button-icon {
  fill: #ffffff;
}

// .video__button:focus {
//   outline: none;
// }

.video:hover .video__button-shape,
.video__button:focus .video__button-shape {
  fill: #ff0000;
  fill-opacity: 1;
}

