.company-title {
  background-color: #fff;
  display: flex;
  align-items: center;

  &__anchor {
    vertical-align: middle;
    display: inline-flex;
    align-items: center;
    color: #000;

    @include for("650") {
      display: flex;
      flex-flow: column;
      justify-content: center;
      width: 100%;
    }
  }

  &__logo {
    width: 100px;
    height: 50px;
    margin: 10px 20px 10px 0;
    margin-right: 50px;

    object-fit: scale-down;
    object-position: center;

    @include for("650") {
      margin-right: 0;
    }
  }

  &__title {
    font-size: var(--fs-h2);

    @include for("650") {
      text-align: center;
    }
  }

  &--shadow {
    min-height: 90px;
    margin: var(--main-indent);
    padding: 0 1.5em;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);

    .company-title {
      &__title {
        font-size: var(--fs-h3);
      }
      &__logo {
        margin-right: 30px;

        @include for("650") {
          margin-right: 0;
        }
      }
    }
  }
}
