.common-queries {
  display: flex;
  flex-wrap: nowrap;

  padding: 10px 0;

  font-size: var(--fs-sm);

  @include for("650") {
    & {
      display: none;
    }
  }

  &__label {
    margin: 5px 0;

    white-space: nowrap;

    color: var(--grey-text);
  }

  &__list {
    display: flex;
    flex-wrap: wrap;

    margin: 0;
    padding: 0;

    list-style: none;
  }

  &__item {
    display: flex;
    align-items: center;

    margin: 5px 0;
    padding: 0 10px;

    border-right: 1px solid var(--grey-light);

    &:last-child {
      border: none;
    }
  }

  &__link {
    text-decoration: none;
    color: var(--purple);
  }
}
