.catalog-card-aside {
  & > * {
    margin-bottom: 15px;
  }

  &__title {
    margin-top: 0;
    font-size: var(--fs-initial);
    font-weight: 500;
  }

  &__title-link {
    text-decoration: none;
    color: inherit;

    // class for js handler
    &--underline {
      text-decoration: underline;
    }
  }

  &__badge-container {
    display: flex;
    margin-right: 10px;
    align-items: center;
    *:not(:last-child) {
      margin-right: 5px;
    }
  }

  &__container {
    display: flex;
    flex-flow: row wrap;
    margin-right: 10px;
  }

  &__grid {
    display: flex;
    flex-wrap: wrap;
    border-bottom: solid 1px var(--grey-lighter);
  }

  &__grid-column {
    flex-grow: 100;

    &:not(:last-child) {
      flex-grow: 1;
    }

    & > * {
      border-top: solid 1px var(--grey-lighter);
    }
  }

  &__grid-item {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    min-height: 45px;
    padding-right: 30px;
  }

  &__grid-item-label {
    width: 135px;
    margin-right: 15px;
    font-size: var(--fs-sm);
  }

  &__grid-item-value {
    font-weight: bold;
  }
}
