.banner-carousel {
  max-width: var(--max-content-width);
  margin-left: auto;
  margin-right: auto;

  @include for("1200<") {
    margin-bottom: 45px;
  }

  .swiper {
    max-width: 1300px;
    background: var(--grey-light);
    background-size: contain;
    @include for("1200<") {
      max-height: 300px;
    }

    @include for("1200") {
      border: 1px solid var(--grey-light);
      background: #fff;
    }
  }

  &--center {
    margin-bottom: 45px;
    padding-left: var(--main-indent);
    padding-right: var(--main-indent);

    @include for("1200") {
      margin-bottom: 30px;
    }

    .swiper {
      margin: 0 auto;
    }
  }

  &--bg-purple-gradient {
    border: none;
    background: var(--purple-gradient);
    background-repeat: no-repeat;
    background-size: 100% 75%;

    .swiper {
      border: none;
      box-shadow: 0 0 30px rgba(0, 0, 0, 0.35);
    }
  }

  &__navigation-buttons {
    position: absolute;
    z-index: 1;
    top: 50%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 0;
    padding: 0 20px;

    @include for("1200") {
      & {
        position: relative;

        /* Высота кнопок + высота padding'ов */
        height: 75px;
        padding: 20px 30px;
      }
    }

    @include for("650") {
      & {
        padding: 20px 15px;
      }
    }
  }

  .swiper-button {
    transition: 0.3s ease opacity;

    @include for("1200<") {
      opacity: 0;
      border: none;
    }

    &:focus {
      opacity: 1;
    }
  }

  &:hover .swiper-button {
    opacity: 1;
  }

  .swiper-pagination {
    position: absolute;
    bottom: -25px;
    left: 50%;
    display: flex;
    transform: translateX(-50%);

    @include for("1200") {
      position: absolute;
      bottom: 35px;
      left: 50%;
      display: flex;
      transform: translateX(-50%);
    }
  }

  &__slide {
    @include for("1200") {
      & {
        height: max-content;
      }
    }

    a {
      text-decoration: none;
      outline: none;

      img {
        width: 100%;
        height: 100%;

        object-fit: contain;
        object-position: 50% 50%;
      }
    }

    &-content {
      position: absolute;
      top: 0;
      left: 0;
      display: flex;
      align-items: center;
      flex-direction: column;
      justify-content: center;
      width: 850px;
      height: 100%;
      min-height: max-content;

      @include for("1200") {
        & {
          position: relative;
          top: unset;
          left: unset;
          align-items: flex-start;
          width: unset;
          height: unset;
        }
      }

      &-title {
        width: 500px;
        margin: 10px 0;
        text-transform: uppercase;
        color: #fff;
        text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.4);
        font-size: var(--fs-h1);
        font-weight: 800;

        @include for("1200") {
          & {
            width: 100%;
            margin: 20px 0 0 0;
            padding: 0 30px;
            text-transform: uppercase;
            color: #000;
            text-shadow: none;
            font-size: var(--fs-h3);
          }
        }

        @include for("650") {
          & {
            padding: 0 15px;
          }
        }
      }

      &-text {
        width: 500px;
        margin: 10px 0;
        color: #fff;
        text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.4);
        font-size: var(--fs-h3);
        font-weight: 600;

        @include for("1200") {
          & {
            width: 100%;
            margin: 10px 0 0 0;
            padding: 0 30px;
            color: #000;
            text-shadow: none;
            font-size: var(--fs-sm);
          }
        }

        @include for("650") {
          & {
            padding: 0 15px;
          }
        }
      }
    }
  }
}
