.contact-list {
  display: table;
  line-height: 2em;

  &__row {
    display: table-row;
  }

  &__term,
  &__definition {
    display: table-cell;
  }

  &__term {
    color: var(--grey-text);
    &:after {
      content: ":";
    }
  }

  &__definition {
    padding-left: 20px;
  }

  &__phone-list {
    padding-left: 0;
    margin: 0;
    list-style: none;
  }
}
