.rating-summary {
  padding-top: 10px;

  &__average,
  &__details {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 20px;
  }

  &__stars::after{
    display: none;
    content: none;
    opacity: 0;
  }

  &__details {
    .rating {
      width: 100%;

      @include for("400<") {
        width: 50%;
        margin-bottom: 5px;
        padding-right: 20px;
      }

      @include for("650<") {
        width: auto;
        padding-right: 20px;
      }
    }
  }

  &__text {
    font-size: var(--fs-normal);
  }
}
