.catalog-settings {
  display: grid;
  padding: 10px 0 5px 0;
  transition: top 0.1s ease;
  background-color: #fff;
  // will-change: top;

  grid-template-columns: minmax(0%, 100%) auto;
  // grid-column-gap: 15px;

  @include for("650") {
    margin-right: var(--negative-main-indent);
    margin-left: var(--negative-main-indent);

    grid-column-gap: 0;
  }

  &__controls {
    display: inline-flex;
    flex-wrap: wrap;
  }

  &__catalog-view-switcher {
    padding: 2.5px 0;
    margin-left: 15px;
  }

  &__tags-panel {
    overflow-x: auto;
    white-space: nowrap;

    scroll-snap-type: x mandatory;

    @include for("650") {
      margin-bottom: 0;
      padding: 0 var(--main-indent) 5px !important;

      scroll-padding-left: var(--main-indent);
      scroll-padding-right: var(--main-indent);
    }

    .tag {
      margin-top: 2.5px;
      margin-bottom: 10px;

      scroll-snap-align: start;
      scroll-snap-stop: normal;
      &:last-child {
        margin-right: 0;
      }
    }

    &.tags-panel--empty {
      flex-grow: 0;
    }

    &.tags-panel--contain {
      flex-grow: 1;
    }
  }

  &__filters {
    display: flex;
    overflow-x: auto;
    flex-grow: 0;
    flex-wrap: wrap;
    padding: 2.5px 0;

    scroll-snap-type: x mandatory;

    @include for("1200") {
      display: inline-block;
      white-space: nowrap;
    }

    @include for("650") {
      margin-bottom: 0;
      padding: 0 var(--main-indent) 5px !important;

      scroll-padding-left: var(--main-indent);
      scroll-padding-right: var(--main-indent);
    }

    > * {
      display: inline-block;
      margin: 0 15px 5px 0;

      scroll-snap-align: start;
      scroll-snap-stop: normal;
      &:last-child {
        margin-right: 0;
      }
    }
  }

  // Modifications
  &--sticky {
    position: sticky;
    z-index: 1;
    top: var(--header-height);
    @include for("900<") {
      top: 65px;
    }

    &::after {
      position: absolute;
      bottom: -5px;
      overflow: hidden;
      width: 100%;
      height: 5px;
      content: "";
      transition: opacity 0.1s linear;
      opacity: 0;
      box-shadow: inset 0 5px 10px -10px rgba(0, 0, 0, 0.7);

      @include for("650<") {
        left: calc(0px - var(--main-indent));
        width: calc(100% + var(--main-indent) * 2);
      }
    }

    &[stuck]::after {
      opacity: 1;
    }

    .catalog-settings__sticky-sentinel {
      position: absolute;
      top: -66px;
      width: 1px;
      height: 66px;

      @include for("900") {
        top: calc(0px - var(--header-height) - 1px);
        height: calc(var(--header-height) + 1px);
      }
    }
  }

  .--map-mode & {
    @include for("900") {
      margin-right: var(--negative-main-indent);
      margin-left: var(--negative-main-indent);

      &__tags-panel {
        margin-bottom: 0;
        padding: 0 var(--main-indent) 5px !important;
        scroll-padding-left: var(--main-indent);
        scroll-padding-right: var(--main-indent);
      }

      &__filters {
        margin-bottom: 0;
        padding: 0 var(--main-indent) 5px !important;
        scroll-padding-left: var(--main-indent);
        scroll-padding-right: var(--main-indent);
      }

      &::after {
        content: none;
      }

      &__catalog-view-switcher {
        display: none;
      }
    }
  }
}
